.circle_1 {
  background-color: #b40101;
  animation: circle1 10s linear infinite;
  position: absolute !important;
  z-index: 0;
}
.circle_2 {
  background-color: #b40101;
  animation: circle1 10s linear 2s infinite;
  position: absolute;
  z-index: 0;
  opacity: 0;
}
.circle_3 {
  animation: circle1 10s linear 4s infinite;
  position: absolute;
  z-index: 0;
  opacity: 0;
}
.circle_4 {
  animation: circle1 10s linear 6s infinite;
  position: absolute;
  opacity: 0;
  z-index: 0;
}
.circle_5 {
  animation: circle1 10s linear 8s infinite;
  position: absolute;
  z-index: 0;
  opacity: 0;
}

.hidden_animation1 {
  animation: hidden-animation 10s linear infinite;
  position: absolute;
  width: 100%;
}
.hidden_animation2 {
  animation: hidden-animation 10s 2s linear infinite;
  opacity: 0;
  position: absolute;
  width: 100%;
}
.hidden_animation3 {
  animation: hidden-animation 10s 4s linear infinite;
  opacity: 0;
  position: absolute;
  width: 100%;
}
.hidden_animation4 {
  animation: hidden-animation 10s 6s linear infinite;
  opacity: 0;
  position: absolute;
  width: 100%;
}
.hidden_animation5 {
  animation: hidden-animation 10s 8s linear infinite;
  opacity: 0;
  position: absolute;
  width: 100%;
}

@keyframes circle1 {
  0% {
    transform: translate(50%, 0);
    right: 50%;
    background-color: #3f51a0;
    opacity: 1;
  }
  10% {
    transform: translate(50%, 0);
    background-color: #3f51a0;
  }
  20% {
    transform: translate(-160%, 150%);
    background-color: #b40101;
  }
  30% {
    transform: translate(-160%, 150%);
  }
  40% {
    transform: translate(-150%, 330%);
  }
  50% {
    transform: translate(-150%, 330%);
  }
  60% {
    transform: translate(240%, 330%);
  }
  70% {
    transform: translate(240%, 330%);
  }
  80% {
    transform: translate(240%, 150%);
    opacity: 1;
  }
  90% {
    transform: translate(240%, 150%);
    opacity: 1;
  }
  95% {
    background-color: #b40101;
    opacity: 1;
  }
  100% {
    transform: translate(50%, 0);
    right: 50%;
    background-color: #3f51a0;
    opacity: 1;
  }
}

@keyframes hidden-animation {
  0% {
    display: block;
    opacity: 1;
  }
  10% {
    display: none;
    opacity: 0;
  }
  20% {
    display: none;
    opacity: 0;
  }
  30% {
    display: none;
    opacity: 0;
  }
  40% {
    display: none;
    opacity: 0;
  }
  50% {
    display: none;
    opacity: 0;
  }
  60% {
    display: none;
    opacity: 0;
  }
  70% {
    display: none;
    opacity: 0;
  }
  80% {
    display: none;
    opacity: 0;
  }
  90% {
    display: none;
    opacity: 0;
  }
  95% {
    display: block;
    opacity: 1;
  }
  100% {
    display: block;
    opacity: 1;
  }
}
